.rr-room_days {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.rr-room_day {
  width: 100%;
}

.rr-room {
  margin-top: 10px;
  border: 1px solid lightgrey;
}

.rr-room_description {
  color: lightgrey;
}

.rr-room_time {
  padding: 3px;
  margin: 1px;
}

.rr-room_free-time {
  cursor: pointer;
}

.rr-room_reserved-time {
  background-color: lightgrey;
  cursor: default;
}

@media (min-width: 860px) {
  .rr-room_day {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    height: 120px;
  }
}


